import "./hero.scss";
import { ROOT_URL, scrollToResults } from "../../utils/utilFuncs";

const Hero = () => {
  return (
    <div className="hero">
      <img
        src={`${ROOT_URL}/images/hero-image.jpg`}
        alt="fitness"
        className="hero-img"
      />
      <h1>
        Fit City <br /> Fitness Club
      </h1>
      <div>
        <p className="slogan">Sweat, Smile and Repeat!</p>
        <p>
          Check out the most effective exercises <br />
          personalized for you
        </p>
        <button onClick={scrollToResults}>Explore Exercises</button>
      </div>
    </div>
  );
};

export default Hero;
