import "./bodyParts.scss";
import React, { useEffect, useState } from "react";
import { ThreeDots } from "react-loader-spinner";
import { ScrollMenu, VisibilityContext } from "react-horizontal-scrolling-menu";

import { fetchBodyParts } from "../../utils/fetchData";
import { scrollToResults } from "../../utils/utilFuncs";
import { ROOT_URL } from "../../utils/utilFuncs";

function LeftArrow() {
  const { isFirstItemVisible, scrollPrev } =
    React.useContext(VisibilityContext);

  return (
    <div
      className="arrow"
      disabled={isFirstItemVisible}
      onClick={() => scrollPrev()}
    >
      <img src={`${ROOT_URL}/images/left-arrow.png`} alt="left-arrow" />
    </div>
  );
}

function RightArrow() {
  const { isLastItemVisible, scrollNext } = React.useContext(VisibilityContext);

  return (
    <div
      className="arrow"
      disabled={isLastItemVisible}
      onClick={() => scrollNext()}
    >
      <img src={`${ROOT_URL}/images/right-arrow.png`} alt="right-arrow" />
    </div>
  );
}

const BodyPartCard = ({
  bodyPart,
  onClick,
  selected,
  title,
  itemId,
  setSearchedText,
}) => {
  const visibility = React.useContext(VisibilityContext);

  function handleClick() {
    visibility.scrollToItem(visibility.getItemByIndex(itemId));
    setSearchedText(bodyPart);
    scrollToResults();
  }

  return (
    <div className="body-part-card" onClick={handleClick} tabIndex={0}>
      <img src={`${ROOT_URL}/images/weightlifter.png`} alt="weightlifter" />
      {bodyPart}
    </div>
  );
};

const BodyParts = ({ setSearchedText }) => {
  const [bodyParts, setBodyParts] = useState({ status: "loading" });

  useEffect(() => {
    fetchBodyParts()
      .then((response) => {
        setBodyParts({
          status: "success",
          data: ["all", ...response.data],
          selected: "all",
        });
      })
      .catch((error) => {
        const message = "Error while fetching body parts from API";
        console.error(message);
        console.error("message : ", error.message);
        setBodyParts({ status: "error", message });
      });
  }, []);

  return (
    <section className="body-parts">
      {bodyParts.status === "loading" ? (
        <div className="loader">
          <ThreeDots color="#19215e" height={100} width={100} />
        </div>
      ) : bodyParts.status === "success" ? (
        <ScrollMenu LeftArrow={LeftArrow} RightArrow={RightArrow}>
          {bodyParts.data.map((bodyPart, id) => (
            <BodyPartCard
              bodyPart={bodyPart}
              itemId={id} // NOTE: itemId is required for track items
              title={id}
              key={id}
              setSearchedText={setSearchedText}
            />
          ))}
        </ScrollMenu>
      ) : (
        <p className="error">{bodyParts.message}</p>
      )}
    </section>
  );
};

export default BodyParts;
