import "./youtubeResults.scss";
import { useState, useEffect } from "react";
import { ThreeDots } from "react-loader-spinner";

import { searchYoutubeVideos } from "../../utils/fetchData";

const YouTubeCard = ({ videoData }) => {
  const youtubeUrl = "https://www.youtube.com/embed/";

  return (
    <section className="youtube-video-card">
      <iframe
        className="youtube-video"
        src={youtubeUrl + videoData.videoId}
        title={youtubeUrl + videoData.videoId}
      ></iframe>
      <h4>{videoData.title}</h4>
      <p>{videoData.channelName}</p>
    </section>
  );
};

const YouTubeResults = ({ exerciseName }) => {
  const [youTubeData, setYouTubeData] = useState([]);
  const [status, setStatus] = useState("loading");

  useEffect(() => {
    (async () => {
      try {
        let youTubeResults = await searchYoutubeVideos(exerciseName);
        setYouTubeData(youTubeResults.data.contents.slice(0, 3));
        setStatus("success");
      } catch (error) {
        console.log(error);
        setStatus("failure");
      }
    })();
  }, [exerciseName]);

  return (
    <article className="youtube-results" id="youtube-results">
      <h3>
        Watch <span className="exercise-name">{exerciseName}</span> exercise
        videos
      </h3>
      {status === "loading" ? (
        <ThreeDots color="#19215e" height={100} width={100} />
      ) : status === "success" ? (
        <section className="youtube-videos">
          {youTubeData.map((videoData) => (
            <YouTubeCard
              key={videoData.video.videoId}
              videoData={videoData.video}
            />
          ))}
        </section>
      ) : null}
    </article>
  );
};

export default YouTubeResults;
