import React from "react";
import { Link } from "react-router-dom";
import "./header.scss";
import { ROOT_URL, scrollToResults } from "../../utils/utilFuncs";

const Header = () => {
  return (
    <header>
      <Link to={ROOT_URL}>
        <img
          src={`${ROOT_URL}/favicon.ico`}
          alt="dumbbells, Fit icons created by Eucalyp - Flaticon"
          className="logo"
        />
      </Link>
      <nav>
        <Link to={ROOT_URL}>Home</Link>
        <Link to="#" onClick={() => setTimeout(scrollToResults, 0)}>
          Exercises
        </Link>
      </nav>
    </header>
  );
};

export default Header;
