import { useState } from "react";
import BackgroundText from "../components/BackgroundText/BackgroundText";
import BodyParts from "../components/BodyParts/BodyParts";
import Hero from "../components/Hero/Hero";
import SearchExercises from "../components/SearchExercises/SearchExercises";
import SearchResults from "../components/SearchResults/SearchResults";

const HomePage = ({ scrollToResults }) => {
  const [searchedText, setSearchedText] = useState("all");

  return (
    <main>
      <Hero scrollToResults={scrollToResults} />
      <BackgroundText />
      <SearchExercises setSearchedText={setSearchedText} />
      <BodyParts setSearchedText={setSearchedText} />
      <SearchResults searchedText={searchedText} />
    </main>
  );
};

export default HomePage;
