import "./exercisePage.scss";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { CirclesWithBar } from "react-loader-spinner";

import { fetchExercisesById } from "../utils/fetchData";
import YouTubeResults from "../components/YouTubeResults/YouTubeResults";
import { ROOT_URL } from "../utils/utilFuncs";

const ExercisePage = () => {
  const { exerciseId } = useParams();
  const [loading, setLoading] = useState("loading");
  const [exerciseDetails, setExerciseDetails] = useState(null);

  useEffect(() => {
    fetchExercisesById(exerciseId)
      .then((response) => {
        setExerciseDetails(response);
        setLoading("success");
      })
      .catch((error) => {
        setLoading("failure");
        console.error(error);
      });

    setTimeout(() => {
      window.scroll(0, 0);
    }, 0);
  }, [exerciseId]);

  return (
    <article className="exercise-details">
      {loading === "loading" ? (
        <div
          className="loader"
          style={{ width: "fit-content", margin: "18vh auto" }}
        >
          <CirclesWithBar
            height="150"
            width="150"
            color="#19215e"
            ariaLabel="circles-with-bar-loading"
          />
        </div>
      ) : loading === "success" ? (
        <>
          <div className="exercise-main-section">
            <img
              src={exerciseDetails.gifUrl}
              alt="exercise"
              className="exercise-image"
            />
            <div className="text-segment">
              <h1 className="exercise-name-heading">{exerciseDetails.name}</h1>
              <p className="exercise-paragraph">
                Exercises keep you strong. {exerciseDetails.name} is one of the
                best exercises to target your {exerciseDetails.target}. It will
                help you improve your mood and gain energy.
              </p>
              <div className="key-details">
                <div className="key-detail-item">
                  <div className="detail-item-icon">
                    <img
                      src={`${ROOT_URL}/images/chronometer.png`}
                      alt="chronometer"
                    />{" "}
                  </div>
                  <h5>{exerciseDetails.bodyPart}</h5>
                </div>
                <div className="key-detail-item">
                  <div className="detail-item-icon">
                    <img
                      src={`${ROOT_URL}/images/heart-rate.png`}
                      alt="heart-rate"
                    />{" "}
                  </div>
                  <h5>{exerciseDetails.target}</h5>
                </div>
                <div className="key-detail-item">
                  <div className="detail-item-icon">
                    <img src={`${ROOT_URL}/images/whistle.png`} alt="whistle" />{" "}
                  </div>
                  <h5>{exerciseDetails.equipment}</h5>
                </div>
              </div>
            </div>
          </div>
          <YouTubeResults exerciseName={exerciseDetails.name} />
        </>
      ) : (
        <div className="error-div">
          <img
            src={`${ROOT_URL}/images/dead.png`}
            alt="error"
            className="dead-img"
          />
          <p className="error">Error while fetching data from server</p>
        </div>
      )}
    </article>
  );
};

export default ExercisePage;
