import "./searchExercises.scss";
import { useState } from "react";
import { scrollToResults } from "../../utils/utilFuncs";

const SearchExercises = ({ setSearchedText }) => {
  const [inputText, setInputText] = useState("");

  const handleSearch = async () => {
    setInputText("");
    setSearchedText(inputText.toLowerCase());
    scrollToResults();
  };

  return (
    <section className="searchExercises">
      <h2>Search for Awesome Exercises</h2>
      <div className="search">
        <input
          type="text"
          placeholder="Search Exercises"
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          onKeyUp={(e) => {
            if (e.key === "Enter") handleSearch();
          }}
        />
        <button onClick={() => handleSearch()}>Search</button>
      </div>
    </section>
  );
};

export default SearchExercises;
