import "./searchResults.scss";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import ReactPaginate from "react-paginate";
import { ThreeDots } from "react-loader-spinner";

import { fetchAllExercises } from "../../utils/fetchData";
import { ROOT_URL } from "../../utils/utilFuncs";

const RightArrow = ROOT_URL + "/images/right-arrow.png";
const LeftArrow = ROOT_URL + "/images/left-arrow.png";

function Items({ currentItems }) {
  const navigate = useNavigate();

  const showExerciseDetails = (exerciseId) => {
    navigate(`${ROOT_URL}/exercise/${exerciseId}`);
  };

  return (
    <section className="results-card-group">
      {currentItems &&
        currentItems.map((item) => (
          <div
            key={item.id}
            className="results-card"
            onClick={() => showExerciseDetails(item.id)}
          >
            <div className="card-image">
              <img src={item.gifUrl} alt={item.name} />
            </div>
            <div className="pills">
              <button className="pill">{item.bodyPart}</button>
              <button className="pill">{item.target}</button>
            </div>
            <h6>{item.name}</h6>
          </div>
        ))}
    </section>
  );
}

function PaginatedItems({ items, itemsPerPage }) {
  // We start with an empty list of items.
  const [currentItems, setCurrentItems] = useState(null);
  const [pageCount, setPageCount] = useState(0);
  // Here we use item offsets; we could also use page offsets
  // following the API or data you're working with.
  const [itemOffset, setItemOffset] = useState(0);

  useEffect(() => {
    const endOffset = itemOffset + itemsPerPage;
    setCurrentItems(items.slice(itemOffset, endOffset));
    setPageCount(Math.ceil(items.length / itemsPerPage));
  }, [itemOffset, itemsPerPage, items]);

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % items.length;

    setItemOffset(newOffset);
  };

  return (
    <>
      <Items currentItems={currentItems} />
      <ReactPaginate
        breakLabel="..."
        nextLabel={<img src={RightArrow} alt="next" className="change-page" />}
        onPageChange={handlePageClick}
        pageRangeDisplayed={(() => (window.innerWidth < 600 ? 1 : 3))()}
        marginPagesDisplayed={1}
        pageCount={pageCount}
        previousLabel={
          <img src={LeftArrow} alt="previous" className="change-page" />
        }
        renderOnZeroPageCount={null}
        className="pagination"
      />
    </>
  );
}

const SearchResults = ({ searchedText }) => {
  const [exercisesToDisplay, setExercisesToDisplay] = useState([]);
  const [status, setStatus] = useState("loading");
  const itemsPerPage =
    window.innerWidth < 600
      ? 5
      : Math.floor((window.innerWidth - 60) / 324) * 2;
  const [allExercises, setAllExercises] = useState([]);

  useEffect(() => {
    fetchAllExercises()
      .then((response) => {
        setAllExercises(response.data);
        setExercisesToDisplay(response.data);
        setStatus("success");
      })
      .catch((error) => {
        console.error(error.message);
        setStatus("error");
      });
  }, []);

  useEffect(() => {
    setExercisesToDisplay(
      searchedText === "all"
        ? allExercises
        : allExercises.filter(
            (exercise) =>
              exercise.name === searchedText ||
              exercise.target === searchedText ||
              exercise.bodyPart.indexOf(searchedText) !== -1 ||
              exercise.equipment === searchedText
          )
    );
  }, [searchedText, allExercises]);

  return (
    <section id="search-results" className="search-results">
      <h2>Showing Results</h2>
      {status === "loading" ? (
        <ThreeDots
          className="loading-indicator"
          color="#19215e"
          height={100}
          width={100}
        />
      ) : status === "success" ? (
        <PaginatedItems
          items={exercisesToDisplay}
          itemsPerPage={itemsPerPage}
        />
      ) : (
        <p className="error">Error while fetching data</p>
      )}
    </section>
  );
};

export default SearchResults;
